import DotActivator from './DotActivator';


function Swipe(func) {
	let videosContainer = document.querySelector('.videosList'),
		activator = new DotActivator(),
		screenWidth;
	this.func = func;
	if (func) {
		let activeDot = document.querySelector('.active'),
			dot;
		if (activeDot === null) {
			dot = 0;
		} else {
			dot = activeDot.dataset.numb;
		}
		
		activator.activate(dot);
		videosContainer.addEventListener('mousedown', this.clickChecker.bind(this));
		videosContainer.addEventListener('touchstart', this.clickChecker.bind(this));
		(document.body.clientWidth >= 993) ? screenWidth = true : screenWidth = false;
		window.addEventListener('resize', () => {
			let dots = document.querySelectorAll('.videoItemNav'),
				activeDot = document.querySelector('.active'),
				dot,videosContainer = document.querySelector('.videosList'),
				left, activator = new DotActivator();			
			if (activeDot === null) {
				dot = 0;
			} else {
				dot = activeDot.dataset.numb;
			}
			if (screenWidth == true && document.body.clientWidth < 993) {
				screenWidth = false;
				dot = dot * 3;
			} else if (screenWidth == false && document.body.clientWidth > 992) {
				screenWidth = true;
				dot = parseInt(dot / 3);
			}
			activator.activate(dot);
			left = 100 * dot;
			videosContainer.style.left = '-' + left + 'vw';
			if (activeDot == null) {
				this.swipePage(dots.length - 1);
			}
		});

		document.querySelector('footer').addEventListener('click', (e) => {
			let dots = document.querySelectorAll('.videoItemNav'),
				dotNumb = e.target.dataset.numb,
				funcArg = '',
				activator = new DotActivator();
			if(dotNumb) {
				this.swipePage(dotNumb);
				activator.activate(dotNumb);
				if(parseInt(dotNumb) > dots.length - 4) {
					func.call(null, funcArg);
				}
			}
		});
	}
}
Swipe.prototype.swipePage = function(i) {
	let videosContainer = document.querySelector('.videosList'),
		left = 0;
	left = 100 * i;
	videosContainer.style.left = '-' + left + 'vw';
}

Swipe.prototype.clickChecker = function(event) {
    let activeDot = document.querySelector('.active'),
        dot,func = this.func,videos = document.querySelector('.videosList'),
        left,
        oldCursorPos,
    	activator = new DotActivator();
    if (activeDot === null) {
        dot = 0;
    } else {
        dot = activeDot.dataset.numb;
    }
    left = 100 * dot;
    
    event.changedTouches ? oldCursorPos = event.changedTouches[0].pageX : oldCursorPos = event.pageX;

    document.addEventListener('mousemove', Move);
    document.addEventListener('touchmove', Move);

   

    function Move(event) {
        let newCursorPos;
        event.changedTouches ? newCursorPos = event.changedTouches[0].pageX : newCursorPos = event.pageX;
        videos.style.left = (-left + newCursorPos - oldCursorPos) + 'vw';
    }

	function finishClick(event) {
		document.removeEventListener('mousemove', Move);
		document.removeEventListener('touchmove', Move);
	    let activeDot = document.querySelector('.active'),
	        dot;
	    if (activeDot === null) {
	        dot = 0;
	    } else {
	        dot = activeDot.dataset.numb;
	    }
	    let videos = document.querySelector('.videosList'),
	        pages = document.querySelectorAll('.videoItemNav'),
	        newCursorPos;
	    event.changedTouches ? newCursorPos = event.changedTouches[0].pageX : newCursorPos = event.pageX;
	    
	    if (newCursorPos < oldCursorPos) {
	        dot++;
	        if (parseInt(dot) >= pages.length - 3) {
	            func.call(this, '');
	            dot++;
	        }
	    } else if (newCursorPos > oldCursorPos && dot != '0') {
	        dot--;

	    }
	    activator.activate(dot);
	    let left = 100 * dot;
	    videos.style.left = '-' + left + 'vw';

	    document.removeEventListener('mouseup', finishClick);
	    document.removeEventListener('touchend', finishClick);
    }

    document.addEventListener('mouseup', finishClick);
    document.addEventListener('touchend', finishClick);

};

export {Swipe};


