function DotActivator() {
	function activate(number) {
		let dotArr = document.querySelectorAll('.videoItemNav'),
			leftDot = parseInt(number) - 2,
			rightDot = parseInt(number) + 2;
		for(let i = 0; i < dotArr.length; i++) {
			if(i == number) {
				dotArr[i].classList.add('active');
			} 
			else {
				dotArr[i].classList.remove('active');	
			}
			if(i > leftDot - 1 && i < rightDot + 1) {
				dotArr[i].style.display = 'inline-block';
			} else {
				dotArr[i].style.display = 'none';	
			}		
		}
	}

	return {
		activate: activate
	}
}

export {DotActivator as default};


