let $ = function(selector) {
	let elList;
    if (typeof selector === "string") {
        elList = document.querySelectorAll(selector);
    } else if (selector.length) {
        elList = selector;
    } else {
        elList = [selector];
    }
    return new jQ(elList);
}

function jQ(elList) {
	for(let i = 0; i < elList.length; i++ ) {
        this[i] = elList[i];
    }
    this.length = elList.length;
}

jQ.prototype.addClass = function(newClass) {
    let res = [];
	this.forEach(function(el) {
		res.push(el);
	});
	if (typeof newClass === 'function') {
		for(let i = 0; i < res.length; i++) {
			
			let classArr = newClass.call(null, i, res[i].className);
			if (classArr) {
				classArr = classArr.split(' ');
				for(let j = 0; j < classArr.length; j++) {
					res[i].classList.add(classArr[j]);
				}
			}
		}
	}
	else {
		let classArr = newClass.split(' ');
		for(let i = 0; i < res.length; i++) {
			for(let j = 0; j < classArr.length; j++) {
				res[i].classList.add(classArr[j]);
			}
		}
	}
}

jQ.prototype.forEach = function(callback) {
    this.map(callback);
    return this;
};

jQ.prototype.map = function (callback) {
    var results = [], i = 0;
    for ( ; i < this.length; i++) {
        results.push(callback.call(this, this[i], i));
    }
    return results;
};

jQ.prototype.children = function(child) {
	if (typeof child === 'undefined') {
		return this.first(function (el) {
            return el.childNodes;
        });
	}
	else {
		let res = [], childNodes = this.first(function (el) {
            return el.childNodes;
        }), childName = child.slice(1, child.length);
        for (let i = 0; i < childNodes.length; i++) {
        	if (childNodes[i].classList.contains(childName)) {
        		res.push(childNodes[i]);
        	}
        }
        return res;
	}
}

jQ.prototype.html = function (html) {
    if (typeof html !== "undefined") {
        this.forEach(function (el) {
            el.innerHTML = html;
        });
        return this;
    } else {
        return this.first(function (el) {
            return el.innerHTML;
        });
    }
};

jQ.prototype.first = function (callback) {
    let resArr = this.map(callback);
    return resArr[0];
};

jQ.prototype.attr = function (attr, val) {
    if (typeof val !== "undefined") {
        return this.forEach(function(el) {
            el.setAttribute(attr, val);
        });
    } else {
        return this.first(function (el) {
            return el.getAttribute(attr);
        });
    }
}

jQ.prototype.append = function(childToAppend) {
	if (typeof childToAppend !== "string") {
		this.forEach(function(el) {
			el.appendChild(childToAppend.cloneNode(true));
		})
	} 
	else {
		this.forEach(function(el) {
			let buff = el.innerHTML;
			el.innerHTML = buff + childToAppend;
		})
	}
	return this;
}

jQ.prototype.css = function(cssValue) {
	
	if (typeof cssValue === "string") {
		let firstEl = this.first(function(el) {
			return el;
		});
		let styleObj = firstEl.style;
		for(let key in styleObj) {
			if (key == cssValue) {
				return styleObj[key];
			}
		}
	}
	else if (typeof cssValue === "object") {
		this.forEach(function(firstEl) {
			for (let key in firstEl.style) {
				for (let custom in cssValue) {
					if (key === custom) {
						firstEl.style[key] = cssValue[custom];
					}
				}
			}
		})
	}
	return this;
}


jQ.prototype.data = function(key, value) {
	if (typeof key === 'undefined' && typeof value === 'undefined'){
		let firstEl = this.first(function(el) {
			return el;
		});
		return firstEl.dataset;
	}
	else if (typeof key === 'string' && typeof value === 'undefined') {
		let firstEl = this.first(function(el) {
			return el;
		});
		let obj = firstEl.dataset;
		for(let k in obj) {
			if(k == key) {
				return obj[k];
			}
		}
	}
	else if (typeof key === 'string' && typeof value !== 'undefined') {
		this.forEach(function(el) {
			el.dataset[key] = value;
		})
	}
	else if (typeof key === 'object' && typeof value === 'undefined') {
		let obj = key;
		this.forEach(function(el) {
			for(let k in obj) {
				el.dataset[k] = obj[k];
			}
		})
	}
	return this;
}

jQ.prototype.on = function(event, selector,callback) {
	if(arguments.length === 2) {
		this.forEach(function(el) {
			el.addEventListener(event,selector)
		});
	}
	else if (arguments.length === 3) {
		let sel = arguments[1],
			callB = arguments[2],
			firstEl = this.first(function(el) {
				return el;
			});
			firstEl.addEventListener(event, function(e) {
				if(e.target && e.target.matches(sel)){
					callB();
				}
			})
	}
	return this;
}


jQ.prototype.each = function(callback) {
	let res = [];
	this.forEach(function(el) {
		res.push(el);
	});
	for (let i = 0; i < res.length; i++) {
		if(callback.call(res[i], i, res[i]) === false) {
			i = res.length;
		}
	}
	return this;
}


jQ.prototype.one = function(event, callback) {
	let firstEl = this.first(function(el) {
		return el;
	});

	firstEl.addEventListener(event, removeEventFunc(firstEl, event, callback));

	function removeEventFunc(el,event, callback) {
		callback.call(el);
		el.removeEventListener(event, callback);
	}
	return this;
}

export {$};


