import {CreateVideoEl} from './CreateVideoEl';
import {Calculator} from './Calculator';
function VideosSearcher() {
	const API = 'AIzaSyDtiB1kty0XxOfMJJyIehzUpDdvkoVi2r0';
	let pageToken = '',
		lastTitle = '';
		 
	function searchVideos(videoTitle) {
		if(videoTitle === '') {
			videoTitle = lastTitle;
		}
		else {
			lastTitle = videoTitle;
		}
		let xhr = new XMLHttpRequest(),
			requestResult,
			videoArr = [],
			url = 'https://www.googleapis.com/youtube/v3/search?key=' + API + '&type=video&part=snippet&pageToken='+ pageToken + '&maxResults='+ '15' +'&q=' + videoTitle;

		xhr.open('GET', url, true);
		xhr.send();

		xhr.onload = () => {
			requestResult = JSON.parse(xhr.responseText);
			pageToken = requestResult.nextPageToken;
			let videosArr = requestResult.items;
			
			videosArr.forEach( (item, index) => {
				let videoObj = {
					link: 'https://www.youtube.com/watch?v=' + item.id.videoId,
					channelTitle: item.snippet.channelTitle,
					description: item.snippet.description,
					date:  item.snippet.publishedAt.substring(0, 10),
					img: item.snippet.thumbnails.high.url,
					title: item.snippet.title,
					like: 0,
					dislike: 0,
					viewers: 0
				}
				let statRequest = new XMLHttpRequest(),
					statLink = 'https://www.googleapis.com/youtube/v3/videos?key='+ API + '&part=statistics&id=' + item.id.videoId;
				statRequest.open('GET', statLink, true);
				statRequest.send();

				statRequest.onload = () => {
					let responseResItem = JSON.parse(statRequest.responseText).items[0];
					videoObj.like = responseResItem.statistics.likeCount || 0;
					videoObj.dislike = responseResItem.statistics.dislikeCount || 0;
					videoObj.viewers = responseResItem.statistics.viewCount || 0;
					let videoItem = new CreateVideoEl(videoObj);
					videoItem.render();
					let —Åalc = new Calculator();
					—Åalc.calculateWidth();
				}
			})
		} 
	}

	return {
		searchVideos: searchVideos
	}
}

export {VideosSearcher};


