import DotActivator from './DotActivator';
import {Swipe} from './Swipe';
import {navDot} from './navDot';
import {$} from './jQuery';

function Calculator() {
	let pages;
	function calculateWidth() {
		let videoArr = document.querySelectorAll('.videoItem'),
			len = videoArr.length,
			videoContainer = document.querySelector('.videosList'),
			pages = document.querySelectorAll('.videoItemNav');
		if (document.body.clientWidth < 993) {
	        videoContainer.style.width = 'calc(100vw * ' + len + ')';
	        this.pages = calcPages(len, 1);
	    } 
	    else if (document.body.clientWidth >= 993) {
	    	videoContainer.style.width = 'calc(33.3vw * ' + len + ')';
	    	this.pages = calcPages(len, 3);
	    }
	    renderDots(this.pages);
	    let swipe = new Swipe(),
	    	activator = new DotActivator();
	    if(pages.length == 1) {
	    	pages[0].classList.add('active');
        	swipe.swipePage(0);
        	activator.activate(0);
	    }
	}
	function renderDots(pages) {
		let dots = document.querySelectorAll('.videoItemNav'),
			dot, dotContainer = document.querySelector('footer');
		if(dots.length < pages) {
			while(dots.length < pages) {
				dot = new navDot(dots.length);
				$('footer').append(dot.html);
				dots = document.querySelectorAll('.videoItemNav');
			}
		} 
		else {
			while(dots.length > pages) {
				let numb = dots.length - 1;
				dot = document.querySelector('.videoItemNav[data-numb="' + numb + '"]');
				dotContainer.removeChild(dot);
				dots = document.querySelectorAll('.videoItemNav');
			}
		}
	}
	function resizeListener() {
		window.addEventListener('resize', this.calculateWidth);
	}
	return {
		calculateWidth: calculateWidth,
		resizeListener: resizeListener,
		renderDots: renderDots
	}
}


function calcPages(videosInAll, videosOnPage){
	let res = videosInAll/videosOnPage;
	return parseInt(res.toFixed(0));
}

export  {Calculator};


