import {$} from './jQuery';

function CreateVideoEl(video) {
	this.HTML = "<li class='videoItem'><a href=" + video.link+" class='title'>" + video.title + "</a><div class='imgHolder'><img src="+ video.img+ " alt='info about video'></div><p class='descripton'>" + video.description + "</p><div class='user'><i class='fa fa-user' aria-hidden='true'></i><p class='userName'>"+ video.channelTitle+ "</p></div><div class='videoInfo'><div class='videoInfoItem'><i class='fa fa-calendar' aria-hidden='true'></i><p>"+video.date+"</p></div><div class='videoInfoItem'><i class='fa fa-users' aria-hidden='true'></i><p>"+ video.viewers + "</p></div><div class='videoInfoItem'><i class='fa fa-heart-o' aria-hidden='true'></i><p>"+ video.like+"</p></div><div class='videoInfoItem'><i class='fa fa-thumbs-o-down' aria-hidden='true'></i><p>"+ video.dislike +"</p></div></div></li>";
}

CreateVideoEl.prototype.render = function() {
	let html = this.HTML;
	$('.videosList').append(html);
}


export {CreateVideoEl};



