import {$} from './jQuery';

function renderHTML() {
	this.HTML = '<header><div class="headerSearch"><input type="text" name="" placeholder="Search" class="searchInput" id="searchInput"/><div class="searchImg" id="searchImg"><i class="fa fa-search" aria-hidden="true"></i></div></div></header><main><ul class="videosList"></ul></main><footer></footer>';
}

renderHTML.prototype.render = function() {
	let html = this.HTML;
	$('body').append(html);
}


export {renderHTML};




